import ReactDOM from "react-dom";
import React, { Component, Fragment } from "react";
import Header from "../Header";
import Footer from "../Footer";
import Projects from "../Projects";
import About from "../About";
import Skills from "../Skills";
import Thoughts from "../Thoughts";
import Quote from "../Quote";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: null
    };
  }

  componentDidMount() {
    document.title = `El Mehdi ASSALI - Portfolio`;
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    if (this.state.onScreen && rect.y - 100 + rect.height <= 0) {
      this.setState({ onScreen: false });
    } else if (!this.state.onScreen && rect.y <= 100) {
      this.setState({ onScreen: true });
      this.props.updateHeader("Thoughts");
    }
  };

  render() {
    return (
      <Fragment>
        <Header active={this.state.active} />
        <main>
          <div className="container-fluid">
            <Quote after />
            <About gray after before />
            <Skills after before />
            <Projects gray after before />
            <Thoughts before />
          </div>
        </main>
        <Footer />
      </Fragment>
    );
  }
}

export default Home;
