const projects = [
  {
    name: "Portfolio",
    technologies: ["Bootstrap 4", "React"],
    description:
      "A website containing my personal and professional information. " +
      "I constantly publish my thoughts about computer science and new technologies. " +
      "The website contains also a description of all the projects I made.",
    category: "React",
    repository: "www.google.com"
  },
  {
    name: "Ada compiler",
    technologies: ["C", "Bash", "Flex"],
    description:
      "Complete compiler/interpreter for a set of the Ada programming language made for pedagogical purposes.",
    category: "Others",
    repository: "https://github.com/assalielmehdi/sample-spark-ada-compiler"
  }
];

const categories = [...new Set(projects.map(project => project.category))];

const projectsByCategory = [
  {
    category: "All",
    projects
  },
  ...categories.map(category => ({
    category,
    projects: projects.filter(project => project.category === category)
  }))
];

export default projectsByCategory;
